




















import { Component, Vue } from "vue-property-decorator";
import { Input, Button } from "ant-design-vue";
import Menus from "./menus/index.vue";
import Type from "@/store/types";

@Component({
  components: { Menus, AInput: Input, AButton: Button }
})
export default class RichText extends Vue {
  placehoder: string = "输入新内容 (回车确认，Ctrl+回车换行)";
  value: string = "";

  clearValue(): void {
    this.value = "";
  }

  ln(e: Event): void {
    e.preventDefault();
    this.$store.commit({
      type: Type.update_text,
      amount: { ln: true, text: this.value }
    });
    this.clearValue();
  }

  confirm(e: Event): void {
    e.preventDefault();
    this.$store.commit({
      type: Type.update_text,
      amount: { ln: false, text: this.value }
    });
    this.clearValue();
  }
}
