

























import { Component, Vue } from "vue-property-decorator";
import MenuItem from "./item.vue";
import { LSets, RSets } from "@/utils/tag/sets";
import Type from "@/store/types";

@Component({
  components: { MenuItem }
})
export default class Menus extends Vue {
  lsets = LSets;
  rsets = RSets;

  curMenu: number = -1;
  onClick(e: Event, i: number): void {
    this.lsets[i].onClick(this.$store.commit, e);
    if (this.curMenu === -1) {
      this.curMenu = i;
    } else {
      this.curMenu = -1;
      this.$store.commit({ type: Type.add_curtag, amount: "" });
    }
  }

  setCurMenu(i: number): boolean {
    return this.lsets[i].type === this.$store.state.tag;
  }

  doClick(e: Event, i: number): void {
    this.rsets[i].onClick(this.$store.commit, e);
  }
}
