import Type from "@/utils/tag/types";
import { TagCount } from "@/store/types";

function initCounter(values: string[]): TagCount {
  let ret: TagCount = {};
  values.forEach(item => {
    ret[item] = 0;
  });
  return ret;
}

function search(danger: string, item: string): boolean {
  if (item == Type.Normal) item = `class="parsed"`;
  return danger.search(item) > 0;
}

export function counter(stack: string[]): TagCount {
  let values = Object.values(Type);
  let ret: TagCount = initCounter(values);
  let i = 0;
  for (i; i < stack.length; i++) {
    values.forEach(item => {
      if (search(stack[i], item)) ret[item] += 1;
    });
  }
  return ret;
}
