import Type, { State } from "./types";
import Parse from "@/utils/tag/parse";
import * as Update from "@/utils/mutation";

interface Payload<A> {
  amount: A;
}

interface IUpdate_text {
  text: string;
  ln: boolean;
}

type Mutation = {
  [index in keyof typeof Type]: (state: State, payload: Payload<any>) => void;
};

const mutations: Mutation = {
  [Type.add_curtag]: function(state: State, payload: Payload<string>): void {
    state.tag = payload.amount;
  },
  [Type.update_text]: function(
    state: State,
    payload: Payload<IUpdate_text>
  ): void {
    if (!payload.amount.text) return; //must have text to update

    if (state.htmlHeap.length > 50) state.htmlHeap.shift(); //maximum sisze of heap

    state.heapIndex += 1; //往后走
    let index = state.heapIndex;
    state.htmlHeap[index] = new Array(...state.htmlHeap[state.heapIndex - 1]); //新状态 == 前一状态

    state.rowtext = payload.amount.text; //原始文字

    let danger = Parse(state.tag, state.rowtext).parseLn(payload.amount.ln);

    if (state.mark > 0) {
      //修改
      state.htmlHeap[index][state.mark] = danger;
      state.mark = -1;
    } else state.htmlHeap[index].push(danger); //添加

    state.tag = "";
  },
  [Type.do]: function(state: State, payload: Payload<string>): void {
    state.do = payload.amount;
  },
  [Type.update_mark]: function(state: State, payload: Payload<number>): void {
    state.mark = payload.amount; //TODO
  },
  [Type.redoundo]: function(state: State, payload: Payload<boolean>): void {
    if (payload.amount) {
      //redo
      if (state.heapIndex < state.htmlHeap.length - 1) {
        state.heapIndex += 1; //往后走
      }
    } else {
      //undo
      if (state.heapIndex > 0) {
        state.heapIndex -= 1; //往前走
      }
    }
  },
  [Type.statistics]: function(state, payload: Payload<undefined>) {
    state.tagCount = Update.counter(state.htmlHeap[state.heapIndex]);
  }
};

export default mutations;
