import Type from "@/store/types";
import { Commit } from "vuex";
import Tag from "./types";

interface IItem {
  type: Tag | string;
  tip: string;
  onClick: (commit: Commit, e: Event) => void;
}

function click(commit: Commit, amount: string, type: string) {
  commit({ type: type, amount });
}
class LItem implements IItem {
  type: Tag;
  tip: string;
  style: string;
  constructor(type: Tag, tip: string, style: string) {
    this.type = type;
    this.tip = tip;
    this.style = style;
  }

  onClick(commit: Commit, e: Event) {
    click(commit, this.type, Type.add_curtag);
  }

  parse(tag: string, text: string): string | false {
    if (tag == this.type)
      return `<span class="${this.type} ${defaultClass}" style="${this.style}">${text}</span>`;
    else return false;
  }
}

class RItem implements IItem {
  type: string;
  tip: string;
  update?: (commit: Commit) => void;
  constructor(type: string, tip: string, update?: (commit: Commit) => void) {
    this.type = type;
    this.tip = tip;
    this.update = update;
  }
  onClick(commit: Commit, e: Event) {
    click(commit, this.type, Type.do);
    if (this.update) this.update(commit);
  }
}

export const defaultClass: string = "parsed";

export const LSets: LItem[] = [
  new LItem(Tag.Bold, "文字加粗", "font-weight: bold"),
  new LItem(Tag.Italic, "文字倾斜", "font-style: italic"),
  new LItem(Tag.Underline, "下划线", "text-decoration: underline"),
  new LItem(Tag.Strikethrough, "删除线", "text-decoration: line-through"),
  new LItem(Tag.Rise, "上标", "vertical-align: super"),
  new LItem(Tag.Fall, "下标", "vertical-align: sub")
];

export const RSets: RItem[] = [
  new RItem("redo", "重做", function(commit: Commit) {
    commit({ type: Type.redoundo, amount: true });
  }),
  new RItem("undo", "撤销", function(commit: Commit) {
    commit({ type: Type.redoundo, amount: false });
  })
];
