import { LSets, defaultClass } from "./sets";

//TODO: 完成语法的拼接
export default function(tag: string, text: string): ParseLn {
  let result: string[] = [];
  let t: string | false;
  LSets.forEach(item => {
    t = item.parse(tag, text);
    t && result.push(t);
  });
  if (result.length < 1)
    result.push(`<span class="${defaultClass}">${text}</span>`); //row text

  parseLn.danger = result.join("");
  return parseLn;
}

class ParseLn {
  danger: string = "";
  parseLn(ln: boolean): string {
    return ln
      ? `${this.danger} <span style='margin-left: -0.5ex;'>\\n</span> <br/>`
      : this.danger;
  }
}
const parseLn = new ParseLn();
